.tab {
  &__list {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 8px;
    @media screen and (min-width: 425px) {
      grid-template-columns: repeat(3, 1fr);
      justify-content: space-between;
    }

    @media screen and (min-width: 800px) {
      gap: 32px;
    }
  }
}
